import { FC } from 'react'
import { faker } from '@faker-js/faker'

import { Modal, ModalProps } from '../'
import CLASSES from './styles'

export type ModalWrapperProps = Omit<ModalProps, 'isFullscreen' | 'title'> & { title?: string }

const CONTENT = faker.lorem.lines(30)

const TEST_ID_CONTSTRAINT = 'test-constraint'

export const ModalWrapper: FC<ModalWrapperProps> = (props) => {
  return (
    <Modal {...props}>
      {CONTENT}
    </Modal>
  )
}

export const ModalExampleHOC = (hocProps: ModalProps): FC<ModalWrapperProps> => (props) => {
  return (
    <ModalWrapper {...hocProps as ModalWrapperProps} {...props} />
  )
}

export const ModalExampleHOCWithConstraint = (hocProps: ModalProps): FC<ModalWrapperProps> => (props) => {
  return (
    <div id={TEST_ID_CONTSTRAINT} style={{ width: '100%', height: '700px', border: '2px solid #000', position: 'relative' }}>
      <ModalWrapper {...hocProps as ModalWrapperProps} {...props} />
    </div>
  )
}

export const BasicModalExample = ModalExampleHOC({
  isOpen: true,
  title: 'Log in'
})

export const DraggableModalExample = ModalExampleHOCWithConstraint({
  isOpen: true,
  title: 'Log in',
  isDraggable: true,
  minWidth: 680,
  constraintId: TEST_ID_CONTSTRAINT
})

export const MinWidthModalExample = ModalExampleHOC({
  isOpen: true,
  title: 'Log In',
  minWidth: 700
})

export const FullscreenModalExample = ModalExampleHOC({
  className: CLASSES.fullscreenModal,
  isFullscreen: true,
  minWidth: 1280,
  isOpen: true,
  onClose: () => alert('onClose called'),
  renderFooterActions: () => (
    <>
      <div>
        <button>Foo</button>
      </div>

      <div className='cf-right-group'>
        <button>Bar</button>
        <button>Baz</button>
      </div>
    </>
  ),
  title: 'Log In'
})
